.logout-button-container {
	position: absolute;
	top: 10px;
	right: 10px;
}

.logout-button {
	background: none;
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 600px) {
	.logout-button-container {
		display: flex;
		position: static;
		justify-content: flex-end;
	}
}
